export default {
	data() {
		return {
			query: {
				username: null,
				password: null,
				phone: null,
				email: null,
				verify: null
			}
		}
	},
	onLoad() {

	},
	methods: {
		// 发送验证码
		async sendVerify(){
			if (typeof this.query.email == 'undefined' || this.query.email == ''){
				uni.showToast({
					icon: none,
					title: '请填写注册时的邮箱地址'
				})
				return;
			}
			let ret = await this.$http({
				url: '/sendVerify',
				data: {address: this.query.email}
			})
			if (ret.code == 0) uni.showToast({title: ret.message})
		},
		// 提交注册信息
		async commitData(){
			let ret = await this.$http({
				url: "/register",
				method: 'POST',
				data: this.query
			})
			if (ret.code == 0){
				uni.showToast({
					title: ret.message
				})
				uni.navigateTo({
					url: '/pages/login/login'
				})
			}else {
				uni.showToast({
					icon: 'none',
					title: ret.message
				})
			}
		}
	}
}
